import React from "react";
import { withScriptjs, withGoogleMap, GoogleMap } from "react-google-maps";

const MapGroup = withScriptjs(
    withGoogleMap(({ defaultData, zoom }) => {
        return defaultData ? (
            <>
                <GoogleMap
                    defaultZoom={zoom}
                    defaultCenter={{
                        lat: defaultData.coordinates.latitude,
                        lng: defaultData.coordinates.longitude,
                    }}
                    options={{
                        streetViewControl: false,
                        fullscreenControl: false,
                        mapTypeControl: false,
                        scrollwheel: false,
                        zoomControl: false,
                        scaleControl: false,
                        draggable: false,
                    }}
                />
            </>
        ) : null;
    })
);

export default MapGroup;
